









import Vue from "vue";
import { Component } from "vue-property-decorator";

@Component
export default class Tags extends Vue {
  get type() {
    return this.$store.state.type;
  }
  get currentTags() {
    if (this.type === "-") {
      return this.$store.state.payTags;
    } else {
      return this.$store.state.incomeTags;
    }
  }
}
